<template>
    <v-row justify="center">
        <v-dialog v-model="dialog" width="600px">
            <template v-slot:activator="{ on }">

                <v-btn class="mb-2" color="primary" dark v-on="on">
                    <i class="fas fa-envelope mr-1"></i>ແຊັດຕອນນີ້
                </v-btn>
            </template>
            <v-card>
                <v-card-title>
                    <span class="headline mx-auto"> ສົ່ງຫາ {{ user.username }}
                     <v-avatar>
                        <v-img :src="$server + user.image"></v-img>
                    </v-avatar>
                    </span>
                </v-card-title>
                <v-card-text>
                    <v-form @submit.prevent="createOrder(auth._id, user._id, portfolio._id)">
                        <v-layout>
                            <v-flex xs12 sm12 md12 lg12>
                                <v-text-field
                                        v-model="message"
                                        class="ml-4"
                                        counter="20">
                                </v-text-field>
                            </v-flex>
                            <v-flex mt-5>
                                <a @click="createOrder(auth._id, user._id, portfolio._id)" class="icon mx-2">
                                    <v-icon size="30">mdi-send</v-icon>
                                </a>
                            </v-flex>
                        </v-layout>
                    </v-form>
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </v-row>
</template>

<script>
    export default {
        props: [
            'auth',
            'user',
            'portfolio'
        ],
        data: () => ({
            message: '',
            dialog: false,
        }),
        methods: {
            async createOrder (employerId, freelancerId, portfolioId) {
              const results = await this.$axios.post('order/create-order', {
                   employerId,
                   freelancerId,
                   portfolioId,
                   message: this.message
               })
                const orderId = results.data.orderId
                this.$router.push({ name: 'MessageFocus', params: { id: orderId } })
                // socket.io emit
                this.$io.emit('create-order')
            }
        }
    }
</script>
